*{
  padding:0;
  margin: 0;
}


body {
  background-color:#FFFFFF;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.btn-color{
  background-color:#f75b5b;
  border-width:0;
  height:42px;
  box-shadow: 0px 1px 6px #f75b5b;
}

.btn-color:hover{
  background-color:#fa8a8a;
}

.btn-color:focus{
  background-color:#fa8a8a;
}


.heading-color{
  color:#9e2c1d !important;
}

.sub-heading-color{
  color:#be2e15;
}

.bottom-right-scroll{
  z-index: 1000;
  position:fixed;
  right: 10px;
  bottom: 10px;
}


.card{
   margin-right:10px !important;
   box-shadow: 0px 0 1px 2px rgba(1, 41, 112, 0.08);
   -webkit-box-shadow:0px 0 1px 2px rgba(1, 41, 112, 0.08);
   -moz-box-shadow:0px 0 1px 2px rgba(1, 41, 112, 0.08);
}

.carousel-indicators button{
  background-color:#F52C2C  !important;
}

.section-heading-text{
  text-align:center;
  font-size: 14px;
  color: #4154f1;
}

.white{
  background-color: white;
}

.card-img{
  transition: 500ms;
}

.card:hover > .card-img{
  height: 45% !important;
}

.w-40{
  width:40% !important;
}

.bg-blue-light{
  background-color:#f6eaeb !important;
}

.text-blue-dark{
  color:#012970;
}
.text-align-none{
  text-align:left;
}

.who-we-cont{
  border-radius:5px 0 0 5px;
}

.about-card{
  box-shadow: 0px 0 30px rgba(1, 41, 112, 0.2);
  margin-right:10px;
  width:22% !important;
}

.text-align-center{
  text-align:center;
}

.model-icon-blue svg{
  color:#f75b5b;
  font-size:50px
}

.model-info-text{
  font-size:15px;
  color:gray;
}

.model-heading{
   font-size:20px;
}

.box p{
  margin-bottom:0 !important ;
  color:gray;
  font-size:16px;
}

.small-icon svg{
  font-size:22px !important;
  margin-right:10px;
}

.border-light-blue{
  border-top:solid 1px #e1ecff !important;
  border-bottom:solid 1px #e1ecff !important;
}

a{
  color:#FA8A8A ;
  text-decoration:none; 
}

a:hover{
  color:#F52C2C ;
}


.border-light-blue-top{
    border-top:solid 1px #e1ecff !important;
}

.banner{
    height: 100vh;
  }

.user-info-form{
  background-color:white;
  margin-top:4% ;
  box-shadow: 0px 5px 25px rgba(65, 84, 241, 0.3);
}

.dropdown-menu.show{
  min-width:7rem !important;
}

.dropdown-item.active{
  color:black;
  background-color:#F5f5f5 !important;
}



.mt-5n{
  margin-top:-6px !important;
}


a > svg{
  color:#FA8A8A ;
  text-decoration:none; 
}

a:hover > svg{
  color:#F52C2C ;
}


.card{
  margin-right:0 !important;
}


@media only screen and (max-width: 995px){
  .nav-bg{
    background-color: white !important;
  }
}


@media only screen and (max-width: 375px){
  .banner h1{
    font-size: 25px !important;
  }
   .user-info-form{
     margin-top:0% !important;
  }
 
}

@media only screen and (max-width: 810px){
  .banner h1{
  font-size: 55px !important;
  }
  .banner{
    height: 80vh !important;
  }

}